.footer {
  font-family: "Inter", Arial, sans-serif;
  max-width: 880px;
  color: #545454;
  margin: 63px auto 60px;
}

.footer__copyrights {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 1.2;
}

@media screen and (max-width: 630px) {
  .footer__copyrights {
    text-align: center;
  }
}

@media screen and (max-width: 320px) {
  .footer__copyrights {
    text-align: left;
    font-size: 14px;
    line-height: 1.21;
  }
}

@media screen and (max-width: 320px) {
  .footer {
    margin: 50px auto 36px;
  }
}
